// components/Dashboard/GoogleAds/index.jsx
import React, { useState, useEffect, useCallback } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  ChevronLeft, 
  ChevronRight, 
  Home,
  CheckCircle,
  AlertCircle,
  Loader2,
  RefreshCw
} from 'lucide-react';
import { Button } from '../../ui/button';
import { Card, CardContent } from '../../ui/card';
import { Progress } from '../../ui/progress';
import { Alert, AlertDescription } from '../../ui/alert';
import { useAppState } from '../../../hooks/useAppState';
import { apiService } from '../../../services/api';

// Import all components
import InstantCampaignGenerator from './InstantCampaignGenerator';
import AIProcessor from './AIProcessor';
import AccountSelector from './AccountSelector';
import CampaignResults from './CampaignResults';
import GoogleOAuth from './GoogleOAuth';
import CampaignDashboard from './CampaignDashboard';

const GoogleAdsIndex = () => {
  // State management
  const {
    currentStep,
    setCurrentStep,
    websiteData,
    setWebsiteData,
    selectedAccount,
    setSelectedAccount,
    campaignData,
    setCampaignData,
    userInfo,
    setUserInfo,
    isAuthenticated,
    setIsAuthenticated
  } = useAppState();

  const [isProcessing, setIsProcessing] = useState(false);
  const [error, setError] = useState(null);
  const [processingSteps, setProcessingSteps] = useState([]);

  // Define workflow steps
  const steps = [
    {
      id: 'website',
      title: 'إدخال الموقع الإلكتروني',
      component: InstantCampaignGenerator,
      description: 'أدخل رابط موقعك الإلكتروني لبدء التحليل'
    },
    {
      id: 'processing',
      title: 'معالجة الذكاء الاصطناعي',
      component: AIProcessor,
      description: 'جاري تحليل موقعك وإنشاء الحملة الإعلانية'
    },
    {
      id: 'auth',
      title: 'ربط حساب Google',
      component: GoogleOAuth,
      description: 'قم بربط حسابك مع Google Ads'
    },
    {
      id: 'account',
      title: 'اختيار الحساب',
      component: AccountSelector,
      description: 'اختر الحساب الذي تريد إنشاء الحملة فيه'
    },
    {
      id: 'results',
      title: 'مراجعة النتائج',
      component: CampaignResults,
      description: 'راجع الحملة المُنشأة قبل الإطلاق'
    },
    {
      id: 'dashboard',
      title: 'لوحة التحكم',
      component: CampaignDashboard,
      description: 'مراقبة وإدارة حملاتك الإعلانية'
    }
  ];

  // Get current step data
  const getCurrentStep = () => steps.find(step => step.id === currentStep) || steps[0];

  // Calculate progress percentage
  const getProgress = () => {
    const stepIndex = steps.findIndex(step => step.id === currentStep);
    return ((stepIndex + 1) / steps.length) * 100;
  };

  // Check authentication status on mount
  useEffect(() => {
    checkAuthStatus();
  }, []);

  // Check authentication status
  const checkAuthStatus = async () => {
    try {
      const response = await apiService.checkGoogleAuthStatus();
      if (response.isAuthenticated) {
        setIsAuthenticated(true);
        setUserInfo(response.userInfo);
        
        // Skip auth step if already authenticated
        if (currentStep === 'auth') {
          setCurrentStep('account');
        }
      }
    } catch (error) {
      console.error('Error checking auth status:', error);
    }
  };

  // Handle website submission
  const handleWebsiteSubmit = useCallback(async (website) => {
    setWebsiteData(website);
    setError(null);
  }, [setWebsiteData]);

  // Handle AI processing completion
  const handleProcessingComplete = useCallback(async () => {
    try {
      // Simulate campaign creation
      const campaign = {
        id: `camp_${Date.now()}`,
        name: `حملة ذكية - ${websiteData?.title || 'موقع إلكتروني'}`,
        website: websiteData,
        status: 'DRAFT',
        createdAt: new Date().toISOString()
      };
      
      setCampaignData(campaign);
      
      // Move to next step based on auth status
      if (isAuthenticated) {
        setCurrentStep('account');
      } else {
        setCurrentStep('auth');
      }
    } catch (error) {
      setError('فشل في إنشاء الحملة الإعلانية');
      console.error('Error creating campaign:', error);
    }
  }, [websiteData, setCampaignData, isAuthenticated, setCurrentStep]);

  // Handle Google authentication success
  const handleAuthSuccess = useCallback((userInfo) => {
    setIsAuthenticated(true);
    setUserInfo(userInfo);
    setCurrentStep('account');
  }, [setIsAuthenticated, setUserInfo, setCurrentStep]);

  // Handle account selection
  const handleAccountSelect = useCallback((account) => {
    setSelectedAccount(account);
  }, [setSelectedAccount]);

  // Handle campaign launch
  const handleCampaignLaunch = useCallback(async (campaign) => {
    setIsProcessing(true);
    setError(null);

    try {
      const response = await apiService.launchCampaign({
        ...campaign,
        accountId: selectedAccount?.id
      });

      if (response.success) {
        setCampaignData(response.campaign);
        setCurrentStep('dashboard');
      } else {
        setError(response.error || 'فشل في إطلاق الحملة');
      }
    } catch (error) {
      setError('حدث خطأ أثناء إطلاق الحملة');
      console.error('Error launching campaign:', error);
    } finally {
      setIsProcessing(false);
    }
  }, [selectedAccount, setCampaignData, setCurrentStep]);

  // Handle step navigation
  const handleNext = useCallback(() => {
    const currentIndex = steps.findIndex(step => step.id === currentStep);
    if (currentIndex < steps.length - 1) {
      setCurrentStep(steps[currentIndex + 1].id);
    }
  }, [currentStep, setCurrentStep]);

  const handlePrevious = useCallback(() => {
    const currentIndex = steps.findIndex(step => step.id === currentStep);
    if (currentIndex > 0) {
      setCurrentStep(steps[currentIndex - 1].id);
    }
  }, [currentStep, setCurrentStep]);

  const handleGoHome = useCallback(() => {
    setCurrentStep('website');
    setError(null);
  }, [setCurrentStep]);

  // Handle campaign actions
  const handleCampaignAction = useCallback(async (action, campaignId) => {
    try {
      const response = await apiService.updateCampaign(campaignId, { action });
      if (response.success) {
        // Refresh campaign data
        // This would typically trigger a re-fetch of campaigns
        console.log(`Campaign ${action} successful`);
      }
    } catch (error) {
      console.error(`Error ${action} campaign:`, error);
    }
  }, []);

  // Render current step component
  const renderCurrentStep = () => {
    const step = getCurrentStep();
    const Component = step.component;

    const commonProps = {
      onNext: handleNext,
      onPrevious: handlePrevious,
      isProcessing,
      error
    };

    switch (step.id) {
      case 'website':
        return (
          <Component
            {...commonProps}
            onWebsiteSubmit={handleWebsiteSubmit}
            website={websiteData}
          />
        );

      case 'processing':
        return (
          <Component
            {...commonProps}
            website={websiteData}
            onProcessingComplete={handleProcessingComplete}
            processingSteps={processingSteps}
            isProcessing={true}
          />
        );

      case 'auth':
        return (
          <Component
            {...commonProps}
            onAuthSuccess={handleAuthSuccess}
            isAuthenticated={isAuthenticated}
            userInfo={userInfo}
          />
        );

      case 'account':
        return (
          <Component
            {...commonProps}
            onAccountSelect={handleAccountSelect}
            selectedAccount={selectedAccount}
          />
        );

      case 'results':
        return (
          <Component
            {...commonProps}
            campaign={campaignData}
            onLaunchCampaign={handleCampaignLaunch}
            onEditCampaign={() => setCurrentStep('website')}
            isLaunching={isProcessing}
          />
        );

      case 'dashboard':
        return (
          <Component
            {...commonProps}
            campaigns={campaignData ? [campaignData] : []}
            onCampaignAction={handleCampaignAction}
            onRefresh={() => window.location.reload()}
          />
        );

      default:
        return <div>خطأ: خطوة غير معروفة</div>;
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50">
      {/* Header with Progress */}
      <div className="sticky top-0 z-40 bg-white/80 backdrop-blur-sm border-b">
        <div className="max-w-7xl mx-auto px-6 py-4">
          <div className="flex items-center justify-between mb-4">
            <div className="flex items-center gap-4">
              <Button
                variant="outline"
                size="sm"
                onClick={handleGoHome}
                className="gap-2"
              >
                <Home className="w-4 h-4" />
                البداية
              </Button>
              
              <div className="hidden md:flex items-center gap-2 text-sm text-muted-foreground">
                <span>الخطوة {steps.findIndex(s => s.id === currentStep) + 1} من {steps.length}</span>
                <span>•</span>
                <span>{getCurrentStep().title}</span>
              </div>
            </div>

            <div className="flex items-center gap-2">
              <Button
                variant="outline"
                size="sm"
                onClick={handlePrevious}
                disabled={currentStep === steps[0].id}
                className="gap-2"
              >
                <ChevronRight className="w-4 h-4" />
                السابق
              </Button>
              
              {currentStep !== 'dashboard' && (
                <Button
                  variant="outline"
                  size="sm"
                  onClick={handleNext}
                  disabled={currentStep === steps[steps.length - 1].id}
                  className="gap-2"
                >
                  التالي
                  <ChevronLeft className="w-4 h-4" />
                </Button>
              )}
            </div>
          </div>

          {/* Progress Bar */}
          <div className="space-y-2">
            <div className="flex justify-between text-xs text-muted-foreground">
              <span>التقدم</span>
              <span>{Math.round(getProgress())}%</span>
            </div>
            <Progress value={getProgress()} className="h-2" />
          </div>
        </div>
      </div>

      {/* Step Indicator */}
      <div className="max-w-7xl mx-auto px-6 py-6">
        <div className="flex items-center justify-center mb-8">
          <div className="flex items-center gap-2 overflow-x-auto pb-2">
            {steps.map((step, index) => {
              const isActive = step.id === currentStep;
              const isCompleted = steps.findIndex(s => s.id === currentStep) > index;
              
              return (
                <React.Fragment key={step.id}>
                  <motion.div
                    initial={{ opacity: 0, scale: 0.8 }}
                    animate={{ opacity: 1, scale: 1 }}
                    transition={{ delay: index * 0.1 }}
                    className={`flex items-center gap-2 px-3 py-2 rounded-full text-sm font-medium transition-all duration-300 ${
                      isActive 
                        ? 'bg-blue-100 text-blue-800 ring-2 ring-blue-500' 
                        : isCompleted 
                          ? 'bg-green-100 text-green-800' 
                          : 'bg-gray-100 text-gray-600'
                    }`}
                  >
                    <div className={`w-6 h-6 rounded-full flex items-center justify-center text-xs ${
                      isActive 
                        ? 'bg-blue-500 text-white' 
                        : isCompleted 
                          ? 'bg-green-500 text-white' 
                          : 'bg-gray-300 text-gray-600'
                    }`}>
                      {isCompleted ? (
                        <CheckCircle className="w-4 h-4" />
                      ) : isActive ? (
                        <Loader2 className="w-4 h-4 animate-spin" />
                      ) : (
                        index + 1
                      )}
                    </div>
                    <span className="hidden sm:inline">{step.title}</span>
                  </motion.div>
                  
                  {index < steps.length - 1 && (
                    <div className={`w-8 h-0.5 ${
                      isCompleted ? 'bg-green-300' : 'bg-gray-200'
                    }`} />
                  )}
                </React.Fragment>
              );
            })}
          </div>
        </div>

        {/* Global Error Display */}
        <AnimatePresence>
          {error && (
            <motion.div
              initial={{ opacity: 0, height: 0 }}
              animate={{ opacity: 1, height: 'auto' }}
              exit={{ opacity: 0, height: 0 }}
              className="mb-6"
            >
              <Alert variant="destructive">
                <AlertCircle className="w-4 h-4" />
                <AlertDescription className="flex items-center justify-between">
                  <span>{error}</span>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => setError(null)}
                    className="ml-4"
                  >
                    إغلاق
                  </Button>
                </AlertDescription>
              </Alert>
            </motion.div>
          )}
        </AnimatePresence>

        {/* Current Step Component */}
        <AnimatePresence mode="wait">
          <motion.div
            key={currentStep}
            initial={{ opacity: 0, x: 20 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: -20 }}
            transition={{ duration: 0.3 }}
          >
            {renderCurrentStep()}
          </motion.div>
        </AnimatePresence>
      </div>

      {/* Loading Overlay */}
      <AnimatePresence>
        {isProcessing && currentStep !== 'processing' && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black/50 backdrop-blur-sm z-50 flex items-center justify-center"
          >
            <Card className="p-6 max-w-sm mx-4">
              <CardContent className="text-center">
                <Loader2 className="w-8 h-8 animate-spin mx-auto mb-4 text-blue-600" />
                <h3 className="font-semibold mb-2">جاري المعالجة...</h3>
                <p className="text-sm text-muted-foreground">
                  يرجى الانتظار بينما نقوم بمعالجة طلبك
                </p>
              </CardContent>
            </Card>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

export default GoogleAdsIndex;
